/* All pages */
body {
    background: #ACE3FF;
    font-family: Roboto;
    min-width: 1100px;
}
nav {
    box-shadow: 0px 4px 4px grey;
    background-color: #FFA9BA;
    padding-bottom: 15px;
    padding-top: 15px;
    margin-bottom: 30px;
    /*visibility: visible;*/
}
p {
    font-size: 30px;
    text-align: center;
}

.link {
    -webkit-text-stroke: 1px black;
    color: white;
    font-size: 24px;
    text-decoration: none;
}
.nav-link {
    -webkit-text-stroke: 1px black;
    color: white;
    font-size: 24px;
    font-weight: bold;
    text-decoration: none;
    margin-right: 30px;
}
